body {
  height: 100vh;
  background-color: #7fffd42d;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

.list {
  list-style: none;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.search-form {
  width: 600px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.search-input {
  color: #000;
  width: 350px;
  height: 50px;
  background: #81e3e32d;
  border: 1px solid #0ff;
  border-radius: 35px;
  outline: none;
  padding: 20px 30px 20px 20px;
  font-family: inherit;
  font-size: 35px;
}

.load-more {
  color: #000;
  text-transform: uppercase;
  cursor: pointer;
  width: 700px;
  background: #585f4353;
  border: 1px solid #0ff;
  border-radius: 45px;
  margin: 20px auto;
  padding: 20px 30px;
  font-size: 28px;
  transition: all .2s ease-in-out;
  display: block;
}

.load-more:hover {
  background: #8fb1ebac;
}

.load-more.is-hidden {
  display: none;
}

.gallery {
  flex-flow: wrap;
  justify-content: center;
  gap: 20px 25px;
  margin: 0 auto;
  list-style: none;
  display: flex;
}

.photo-card {
  flex-basis: calc(33.3333% - 33.3333px);
  margin: 0;
}

.photo-img {
  width: 100%;
  max-width: unset;
  object-fit: cover;
  margin: 0;
  box-shadow: 1px 2px 18px #00000063;
}

.search-btn {
  height: 86px;
  width: 95px;
  color: #000;
  cursor: pointer;
  background: #81e3e32d;
  border: 1px solid #0ff;
  border-radius: 25px;
  outline: none;
  margin: 15px;
  transition: background .3s ease-in-out;
}

.search-btn:hover {
  background: #88e1ffac;
}

.target-element {
  display: none;
}

.target-element.is-visible {
  display: block;
}

.info {
  text-shadow: 1px -2px 3px #170a069e;
  color: #040404;
  background: #fff;
  gap: 15px;
  display: flex;
}

